<template>
  <div>
    <a-modal 
      v-model="visible" 
      :confirmLoading="loading" 
      :maskClosable="false" 
      @cancel="cancel" 
      @ok="confirm" 
      :width="800"
    >
      <div slot="title">商品详细数据</div>
      <div class="modal-body">
        <h3>基本信息</h3>
        
        <div class="row">
          <div class="columns">
            <img :src="form.primary_image" style="width: 50px; height: 50px;" alt="产品主图" />
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">ID:</span>
            <span class="value">{{ form.id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">名称:</span>
            <span class="value">{{ form.name }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">offer_id:</span>
            <span class="value">{{ form.offer_id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">sku:</span>
            <span class="value">{{ form.sku }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">product_id:</span>
            <span class="value">{{ form.product_id }}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">fbo佣金百分比:</span>
            <span class="value">{{ form.fbo_commission_percent}}%</span>
          </div>
          <div class="columns">
            <span class="label">fbo佣金额:</span>
            <span class="value">{{ form.fbo_commission_value}}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">fbp佣金百分比:</span>
            <span class="value">{{ form.fbp_commission_percent}}%</span>
          </div>
          <div class="columns">
            <span class="label">fbp佣金额:</span>
            <span class="value">{{ form.fbp_commission_value}}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">fbs佣金百分比:</span>
            <span class="value">{{ form.fbs_commission_percent}}%</span>
          </div>
          <div class="columns">
            <span class="label">fbs佣金额:</span>
            <span class="value">{{ form.fbs_commission_value}}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">rfbs佣金百分比:</span>
            <span class="value">{{ form.rfbs_commission_percent}}%</span>
          </div>
          <div class="columns">
            <span class="label">rfbs佣金额:</span>
            <span class="value">{{ form.rfbs_commission_value}}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">货币单位:</span>
            <span class="value">{{ form.currency_code}}</span>
          </div>
        </div>

        <div class="row">
          <div class="columns">
            <span class="label">店铺名称:</span>
            <span class="value">{{ form.shop?form.shop.name:"暂无" }}</span>
          </div>
          <div class="columns">
            <span class="label">负责人员:</span>
            <span class="value">{{ form.owner?form.owner.name:"暂无" }}</span>
          </div>
        </div>
        
        <h3 style="margin-top: 10px;">关联系统内商品信息</h3>
        <div v-for="(system_product, index) in form.system_products" :key="index" style="display: flex;">
          <span style="margin-right: 8px;">{{ index+1 }}. </span>
          <div>
            <div class="row">
              <div class="columns">
                <span class="label">id:</span>
                <span class="value">{{ system_product.id }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">system_sku:</span>
                <span class="value">{{ system_product.system_sku }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">参考重量:</span>
                <span class="value">{{ system_product.reference_weight }}</span>
              </div>
              <div class="columns">
                <span class="label">参考价格:</span>
                <span class="value">{{ system_product.reference_cost }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns" style="display: flex; align-items: center;">
                <span class="label" style="width: 71px;">采购链接:</span>
                <div>
                  <div v-for="link,index in JSON.parse(system_product.purchase_link)" :key="link+index">
                    <span>链接{{ index+1 }}号：</span>
                    <a :href="link.link" target="_blank"> {{ link.link }} </a>
                    <span> {{ link.msg }} </span>
                  </div>
                </div>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">供应商名称:</span>
                <span class="value">{{ system_product.supplier_name?system_product.supplier_name:"暂无" }}</span>
              </div>
              <div class="columns">
                <span class="label">采购平台:</span>
                <span class="value">{{ system_product.purchase_platform }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">待采购量:</span>
                <span class="value">{{ system_product.wait_for_purchase_quantity }}</span>
              </div>
              <div class="columns">
                <span class="label">在篮量:</span>
                <span class="value">{{ system_product.in_basket_quantity }}</span>
              </div>
              <div class="columns">
                <span class="label">在途量:</span>
                <span class="value">{{ system_product.in_transit_quantity }}</span>
              </div>
            </div>

            <div class="row">
              <div class="columns">
                <span class="label">库存量:</span>
                <span class="value">{{ system_product.stock_quantity }}</span>
              </div>
              <div class="columns">
                <span class="label">出库量:</span>
                <span class="value">{{ system_product.out_stock_quantity }}</span>
              </div>
              <div class="columns">
                <span class="label">丢失量:</span>
                <span class="value">{{ system_product.loss_quantity }}</span>
              </div>
            </div>
          </div>
        </div>
    </div>
    </a-modal>
  </div>
</template>

<script>
import moment from 'moment'

export default {
  name: 'FormModal',
  props: ['visible', 'form'],
  model: { prop: 'visible', event: 'cancel' },
  data() {
    return {
      loading: false,
    };
  },
  computed: {
  },
  methods: {
    confirm() {
      this.$emit('cancel', false);
    },
    cancel() {
      this.$emit('cancel', false);
    },
  },
}
</script>

<style scoped>
.modal-body { 
  max-height: 60vh;
  overflow-y: auto; 
}

.row {
  display: flex;
  align-items: center;
  margin-top: 5px;
}

.row .columns .label{
  margin-right: 5px;
}

.row .columns .value{
  margin-right: 5px;
  color: green;
}
</style>